import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import ImageListItemBar from "@mui/material/ImageListItemBar";

const dispatchedEvents = [];

const SrsMuiImageListItemBar = function (props, ref) {
  return (
    <ImageListItemBar
      title={props.title}
      subtitle={props.subtitle}
      position={props.position}
      actionIcon={props.actionIcon}
      actionPosition={props.actionPosition}
      theme={props.muiTheme}
    />
  );
};

const styles = `
  /* Custom styles for the MuiImageListItemBar component */
`;

const props = [
  {
    name: "title",
    defaultValue: "",
    type: "string",
  },
  {
    name: "subtitle",
    defaultValue: "",
    type: "string",
  },
  {
    name: "position",
    defaultValue: "bottom",
    type: "string",
  },
  {
    name: "actionIcon",
    defaultValue: null,
    type: "string",
  },
  {
    name: "actionPosition",
    defaultValue: "right",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to ImageListItemBar if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiImageListItemBar),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
